.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.25);
  visibility: visible;
  opacity: 1;
  @include transition(visibility);
  @include transition(opacity);
  overflow-y: auto;
}

/* .is-hidden */
.visually-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.wrapper-modal {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100%;
  padding: 20px 0px;
}

.modal-window {
  position: relative;
  padding: 48px 20px 41px;
  background: var(--header-text);

  @media screen and (min-width: 768px) {
    display: flex;
    padding-left: 30px;
    padding-right: 30px;
  }
}

.close-btn {
  position: absolute;
  top: 10px;
  right: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 30px;
  height: 30px;
  background: var(--header-text);
  fill: var(--title-text);
  border: none;
  cursor: pointer;

  &-svg:hover,
  &-svg:focus {
    fill: var(--accent-color);
  }
}

.close {
  width: 18px;
  height: 18px;
}

.card-poster {
  @media screen and (min-width: 768px) {
    margin-right: 32px;
  }
}
.card-poster__img {
  padding-bottom: 30.5px;

  @media screen and (min-width: 768px) {
    min-width: 264px;
    height: 374px;
    padding-bottom: 0;
  }
  @media screen and (min-width: 1024px) {
    min-width: 396px;
    height: 478px;
  }
}

.card-records__title {
  display: block;
  margin-top: 0;
  margin-bottom: 20px;
  font-weight: 500;
  font-size: 20px;
  line-height: 23px;
  color: var(--title-text);
  text-transform: uppercase;
  @media screen and (min-width: 1024px) {
    font-size: 30px;
    line-height: 35.16px;
  }
}

.card-records__info {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  padding-bottom: 20px;
}

.card-records__items {
  display: block;
  width: 40%;
  padding-left: 0;
  margin: 0;
}

.card-records__item {
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  color: var(--modal-titles-color);

  &:not(:last-child) {
    margin-bottom: 10px;
  }
}

.card-records__subitems {
  display: block;
  padding-left: 0;
  margin: 0;
}
.card-records__subitem {
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  color: var(--title-text);

  &:not(:last-child) {
    margin-bottom: 10px;
  }
}

.card-records__vote {
  display: inline-block;
  text-align: center;
  width: 36px;
  height: 16px;
  border-radius: 5px;
  border: none;
  border-radius: 5px;
  border: none;
  color: var(--header-text);
  background: var(--rating-color);
}

.card-records__votes {
  display: inline-block;
  text-align: center;
  width: 36px;
  height: 16px;
  border-radius: 5px;
  border: none;
  color: var(--title-text);
  background: var(--backgr-footer);
}

.card-records_description {
  font-weight: 500;
  font-size: 12px;
  color: var(--title-text);
  line-height: 20px;
}

.card-records__about {
  margin: 0;
  padding-bottom: 10px;
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  color: var(--title-text);
  text-transform: uppercase;
}

.uppercase {
  text-transform: uppercase;
}

.card-buttons {
  display: flex;
  justify-content: space-between;
  margin-top: 21px;

  @media screen and (min-width: 1024px) {
    justify-content: flex-start;
  }
}

.card-button {
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  width: 112.58px;
  height: 44px;
  border: 1px solid var(--title-text);
  background: transparent;
  box-sizing: border-box;
  border-radius: 5px;
  text-transform: uppercase;
  cursor: pointer;

  &:hover {
    border: none;
    color: var(--header-text);
    background: var(--rating-color);
    @include transition(color);
    @include transition(background);
    @include transition(border);
  }

  @media screen and (min-width: 768px) {
    width: 125px;
    height: 44px;
  }

  @media screen and (min-width: 1024px) {
    width: 136px;
    height: 44px;

    &:not(:last-child) {
      margin-right: 15px;
    }
  }
}
